import React from 'react'
import Breadcrumb from '../common/Breadcrumb'
import DashBoardList from './DashBoardList'

export default function MyDashboard() {
    let pageName = 'My Dashboard'
    return (
        <div>
            <Breadcrumb pageName={pageName} />
            <DashBoardList />
        </div>
    )
}
